											symbolPosition: 'front',
											symbolSpacing: true
										})}}
									</span>
								</template>

							</v-data-table>												
						</div>										
					</v-flex>

					<v-flex sm3>
						<v-select
							:items="arrBaseMoneda"
							v-model="basemoneda"
							label="Moneda Base"
							item-value="valor"
							item-text="texto"
